<!-- eslint-disable -->
<template>
  <div class="clinica-wrapper">
    <div id="clinica">
      <div class="copy-wrapper">
        <div class="title main movement">{{title}}</div>
        <div class="line movement"></div>
        <div class="copy movement" v-html="content">
          <!-- Somos uma Clínica de Psicologia. Que abraça a infância, a adolescência e a adultez.
          Que considera o ato psicológico (do processo de avaliação ao aconselhamento e
          intervenção psicológica) como de charneira entre a confiança na qualidade dos
          serviços profissionais especializados e a confiança de que, no espaço da relação
          clínica, se promova a saúde mental e familiar. -->
        </div>
      </div>
      <div class="ctas">
        <router-link :to="url">
          <div class="btn blue lateral">
            <div class="txt">{{cta}}</div>
          </div>
        </router-link>

        <div class="btn white lateralR" @click="openContactForm()">
          <div class="txt">Contate-nos</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import axios from "axios";

export default {
  data() {
    return {
      title: '',
      content: '',
      cta: '',
      url: ''
    };
  },
  methods: {
    offsetTop() {
      var slideshow = $(".slideshow-wrapper");
      var aboutSection = $(".clinica-wrapper");
      aboutSection.css("margin-top", slideshow.height());
    },
    openContactForm() {
      var contact_form = document.querySelector("#contact-form-2");
      var menu = document.querySelector("#menu");

      contact_form.style.transition = ".5s";
      contact_form.style.transform = "translate(-50%, -50%)";
      contact_form.style.pointerEvents = "all";
      $("#contact-form-2 .page").css("pointer-events", "all");
      var html_elem = document.documentElement;
      html_elem.style.overflow = "hidden";

      $(".menu-wrapper-mobile .menu-items").removeClass("open");
      $(".menu-wrapper-mobile .btn-menu").removeClass("open");
      $(".menu-wrapper-mobile .close-menu").removeClass("open");

      $("#contact-form-2 .p0 .text").animate({ left: 0, opacity: 1 }, 500);
    }
  },
  mounted() {
    this.offsetTop();

    var map = Array.prototype.map;
    var vm = this;

    axios
      .get("https://www.anacarolinapereira.pt/db-api/getAboutHomepage.php")
      .then(resp => {
        const data = resp.data;

        vm.title = data[0].title;
        vm.content = data[0].content;
        vm.cta = data[0].cta_txt;
        vm.url = data[0].link;
      })
  },
  created() {
    window.addEventListener("resize", this.offsetTop);
  },
  destroyed() {
    window.removeEventListener("resize", this.offsetTop);
  }
};
</script>

<style lang="scss" scoped>
//colors
$white: #fff;
$black: #000;
$dk-gray-1: #393939;
$dk-gray-2: #464646;
$dk-gray-3: #707070;
$lt-gray: #f6f6f6;
$turquiose: rgba(11, 171, 197, 0.6);

@function pxToEm($value) {
  $point: ($value * 3) / 4;
  $yem: $point / 12;

  @return $yem * 1rem;
}

//paddings and margins
$pd-desktop: 0 pxToEm(130);
$mg-desktop: 0 pxToEm(40);

//circle
$circle: 50%;

.clinica-wrapper {
  background-color: $white;
  height: pxToEm(390);
  padding: $pd-desktop;
  overflow: hidden;

  @media only screen and (min-width: 1200px) and (max-width: 1919px) {
    padding: 0 pxToEm(80);
  }

  @media only screen and (min-width: 992px) and (max-width: 1199px) {
    padding: 0 pxToEm(60);
  }

  @media only screen and (min-width: 768px) and (max-width: 991px) {
    padding: 0 pxToEm(40);
  }

  @media only screen and (min-width: 600px) and (max-width: 767px) {
    padding: 0 pxToEm(20);
  }

  @media only screen and (min-width: 300px) and (max-width: 599px) {
    height: auto;
    padding: 0 pxToEm(20);
  }

  #clinica {
    height: 100%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    max-width: pxToEm(1400);

    .copy-wrapper {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: space-between;
      overflow: hidden;
      padding-bottom: pxToEm(40);

      .title {
        margin-top: pxToEm(60);
        color: $dk-gray-3;

        @media only screen and (min-width: 300px) and (max-width: 599px) {
          margin-top: pxToEm(40);
        }
      }

      .line {
        width: pxToEm(79);
        height: pxToEm(1);
        background-color: $turquiose;
        margin-top: pxToEm(15);
      }

      .copy {
        font-size: pxToEm(20);
        max-width: pxToEm(1200);
        text-align: center;
        width: 100%;
        color: $dk-gray-3;
        font-family: "OS-Regular";
        margin-top: pxToEm(20);
      }
    }

    .ctas {
      margin-bottom: pxToEm(80);
      max-width: pxToEm(305);
      justify-content: space-between;
      display: flex;
      width: 100%;

      @media only screen and (min-width: 300px) and (max-width: 599px) {
        flex-direction: column;
        align-items: center;
        margin-bottom: pxToEm(40);
      }

      .btn {
        width: pxToEm(131);
        height: pxToEm(44);

        @media only screen and (min-width: 300px) and (max-width: 599px) {
          margin-bottom: pxToEm(20);
        }

        .txt {
          font-family: Arial, Helvetica, sans-serif;
          font-size: pxToEm(14);
          font-weight: bold;
        }
      }
    }
  }
}
</style>



